import { gql } from "@apollo/client";

export const ALL_PLAN_MARKS = gql`
    query allPlanMarks {
        allPlanMarks {
            id
            name
        }
    }
`

export const CREATE_PLAN_MARK = gql`
    mutation createPlanMark ($name: String) {
        createPlanMark (name: $name) {
            planMark {
                id
            }
        }
    }
`

export const UPDATE_PLAN_MARK = gql`
    mutation updatePlanMark ($id: ID, $name: String) {
        updatePlanMark (id: $id, name :$name) {
            planMark {
                id
            }
        }
    }
`

export const DELETE_PLAN_MARK = gql`
    mutation deletePlanMark ($id: ID) {
        deletePlanMark (id: $id) {
            planMark {
                name
            }
        }
    }
`

export const ALL_PLANS = gql`
    query allPlans {
        allPlans {
            id
            title
            teacher {
                name
                familyName
            }
            subject {
                id
                subject
            }
            section {
                id
                program {
                    id
                }
            }
            schoolyear {
                id
            }
            approvedBy {
                name
                familyName
            }
            topic
            subjectTopic
            intention
            keyword
            consumables
            duration
        }
    }
`

export const PLAN_BY_ID = gql`
    query planById($id: ID!) {
        planById(id: $id) {
            id
            title
            teacher {
                name
                familyName
            }
            subject {
                subject
            }
            section {
                classes {
                    classes
                }
                section
            }
            schoolyear {
                schoolyear
            }
            approvedBy {
                name
                familyName
            }
            topic
            subjectTopic
            intention
            keyword
            consumables
            duration
        }
    }
`

export const ALL_PLAN_ACTIONS_BY_PLAN = gql`
    query allPlanActionsByPlan($plan: ID!) {
        allPlanActionsByPlan(plan: $plan) {
            id
            planMark
            name
            teachingMethod
            teacherActivity
            studentActivity
            studentAssignment
            duration
        }
    }
` 

export const CREATE_PLAN = gql`
    mutation createPlan(
        $consumables: String
        $duration: Int
        $intention: String
        $keyword: String
        $schoolyear: ID
        $section: ID
        $subject: ID
        $subjectTopic: String
        $title: String
        $topic: String
    ) {
        createPlan (
            consumables : $consumables
            duration : $duration
            intention : $intention
            keyword : $keyword
            schoolyear : $schoolyear
            section : $section
            subject : $subject
            subjectTopic : $subjectTopic
            title: $title
            topic : $topic
        ) {
            plan {
                id
            }
        }
    }
`

export const UPDATE_PLAN = gql`
    mutation updatePlan(
        $consumables: String
        $duration: Int
        $id: ID
        $intention: String
        $keyword: String
        $schoolyear: ID
        $section: ID
        $subject: ID
        $subjectTopic: String
        $title: String
        $topic: String
    ) {
        updatePlan(
            consumables: $consumables
            duration: $duration
            id: $id
            intention: $intention
            keyword: $keyword
            schoolyear: $schoolyear
            section: $section
            subject: $subject
            subjectTopic: $subjectTopic
            title: $title
            topic: $topic
        ) {
            plan {
                id
            }
        }
    }
`

export const APPROVE_PLAN = gql`
    mutation approvePlan ($plan: ID) {
        approvePlan (plan: $plan) {
            plan {
                id
            }
        }
    }
`

export const DELETE_PLAN = gql`
    mutation deletePlan($id: ID) {
        deletePlan(id: $id){
            plan {
                topic
            }
        }
    }
`

export const CREATE_PLAN_ACTION = gql`
    mutation createPlanAction(
        $duration: Int
        $name: String
        $plan: ID
        $planMark: String
        $studentActivity: String
        $studentAssignment: String
        $teacherActivity: String
        $teachingMethod: String
    ) {
        createPlanAction (
            duration: $duration
            name: $name
            plan: $plan
            planMark: $planMark
            studentActivity: $studentActivity
            studentAssignment: $studentAssignment
            teacherActivity: $teacherActivity
            teachingMethod: $teachingMethod
        ) {
            planAction {
                id
            }
        }
    }
`

